.education {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: auto;
  padding-bottom: 5rem;
}

.education .icon {
  color: #00abf0;
  font-size: 2.5rem;
}

.education .education-heading {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.education .education-row {
  display: flex;
  gap: 5rem;
}

.education-row .education-column {
  flex: 1 1 40rem;
}

.education-column .title {
  font-size: 2.5rem;
  margin: 0 0 1.5rem 2rem;
}

.education-column .education-box {
  border-left: 0.2rem solid #00abf0;
}

.education-box .education-content {
  position: relative;
  padding-left: 2rem;
}

.education-box .education-content::before {
  content: "";
  position: absolute;
  top: 0;
  left: -1.1rem;
  width: 2rem;
  height: 2rem;
  background: #00abf0;
  border-radius: 50%;
}

.education-content .content {
  position: relative;
  padding: 1.5rem;
  border: 0.2rem solid #00abf0;
  border-radius: 0.6rem;
  margin-bottom: 2rem;
  overflow: hidden;
}

.education-content .content::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #112e42;
  z-index: -1;
  transition: 0.5s;
}

.education-content .content:hover::before {
  width: 100%;
}

.education-content .content .year {
  font-size: 1.5rem;
  color: #00abf0;
  padding-bottom: 0.5rem;
}

.education-content .content .year .icon {
  padding-right: 0.5rem;
  font-size: larger;
}

.education-content .content h3 {
  font-size: 2rem;
}

.education-content .content p,
.education-content .content a {
  font-size: 1.6rem;
  padding-top: 0.5rem;
}

.education-content .content .link {
  color: #ededed;
  text-decoration: underline;
}

.education-content .content .link:hover {
  color: #00abf0;
}

.education-content .content .link .link-icon {
  padding-left: 2px;
}

@media screen and (max-width: 480px) and (min-width: 320px) {
  .education .education-row {
    flex-direction: column;
  }
}
